import React from 'react'
import { useParams } from 'react-router-dom';
import {todos as items} from '../../data/products.jsx';
const Products = () => {

    const {slug} = useParams();

    const item = items.find((x)=> x.slug===slug);

  return (
    <div>
        <p>{item.id}</p>
        <p>{item.todo}</p>
        <p>{item.slug}</p>
    </div>
  )
}

export default Products;